.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-primary);
	overflow: hidden;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-99 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 80%;
		.image-element {
			height: 100%;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: 50% 100%;
				opacity: 0.5;
			}
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translate(-50%, 0);
	}
	.orn-2 {
		position: absolute;
		bottom: 5%;
		right: 0;
		width: 140px;
		transform: translate(50%, 0) rotate(-10deg);
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		height: 100vh;
		@include max-lg {
			padding: 4rem 0 6.5rem;
			align-items: center;
		}
		.cover-content {
			position: relative;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				width: 35%;
				min-height: 70%;
				background-color: var(--color-main);
				color: var(--color-dark);
				border-radius: var(--border-radius);
				@include max-xl {
					width: 40%;
				}
				@include max-lg {
					width: 70%;
				}
				@include max-md {
					width: 100%;
				}
				.cover-description {
					position: relative;
					width: 100%;
					padding: 2rem;
					text-align: center;
					@include max-lg {
						padding: 2rem 1rem;
					}
					.cover-event {
						position: relative;
						margin-bottom: 1rem;
					}
					.greeting-wrapper {
						position: relative;
						margin-bottom: 1rem;
					}
					.title-cover {
						margin-bottom: 0;
						display: flex;
						flex-wrap: wrap;
						align-items: center;
						justify-content: center;
						column-gap: 0.5rem;
						padding: 0.5rem 0;
						@include max-sm {
							flex-direction: column;
						}
					}
					p {
						margin-bottom: 0;
					}
					h6 {
						margin-bottom: 0;
						font-weight: 600;
					}
				}
			}
			.elements-widget {
				position: relative;
				width: 100%;
			}
		}
	}
}
