.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-99 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		aspect-ratio: 7/8;
		.image-element {
			height: 100%;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: 50% 100%;
				opacity: 0.5;
			}
		}
	}
	.appology-wrap {
		position: relative;
		background-color: var(--color-primary);
		color: var(--color-light);
		border-radius: var(--border-radius);
		padding: 3rem 2rem;
		overflow: hidden;
		@include max-md {
			padding: 2rem 1rem;
		}
	}
}
