.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
		.container {
			padding: 0;
		}
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 120px;
		transform: translate(-50%, 50%);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 120px;
		transform: translate(50%, 50%) scaleX(-1);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.rsvp-wrapper {
		position: relative;
		background-color: var(--color-primary);
		color: var(--color-light);
		border-radius: var(--border-radius);
		padding: 20rem 3rem 3rem;
		overflow: hidden;
		@include max-md {
			padding: 18rem 2rem 2rem;
		}
		.orn-99 {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 80%;
			.image-element {
				height: 100%;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					object-position: 50% 100%;
				}
			}
		}
		.orn-98 {
			position: absolute;
			top: 1.5rem;
			left: 50%;
			width: 128%;
			transform: translate(-50%, 0);
		}
		.orn-97 {
			position: absolute;
			top: 0;
			left: 50%;
			width: 150%;
			transform: translate(-50%, -30%);
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
			}
		}
	}
}
