.venue-section {
	position: relative;
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		margin-top: -3rem;
		@include max-md {
			aspect-ratio: 5/6;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.venue-background {
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
	.frame {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 128%;
		aspect-ratio: 5/6;
		transform: translate(-50%, -50%);
		overflow: hidden;
		@include max-md {
			width: 130%;
		}
		.image-element {
			height: 100%;
			img {
				width: 100%;
				height: 100%;
			}
		}
	}
	.orn-99 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 5/6;
		}
		.image-element {
			height: 100%;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: 50% 100%;
				opacity: 0.2;
			}
		}
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 150%;
		opacity: 0.15;
		transform: translate(0, -50%);
	}
	.venue-wrapper {
		position: relative;
		padding: 0 0 5rem;
		@include max-md {
			padding: 0 0 3rem;
		}
		.venue-description {
			position: relative;
			background-color: var(--color-primary);
			color: var(--color-light);
			border-radius: var(--border-radius);
			margin: -3rem auto 8rem;
			text-align: center;
			overflow: hidden;
			@include max-lg {
				margin-bottom: 5rem;
			}
			&-body {
				position: relative;
				padding: 3rem 2rem;
				@include max-md {
					padding: 2rem 1rem;
				}
			}
		}
		.venue-content {
			position: relative;
			.ribbon-venue {
				position: relative;
				padding: 1rem 0;
				svg {
					width: 40px;
					height: auto;
					path {
						stroke: var(--color-dark);
					}
				}
				.st0 {
					fill: var(--color-secondary);
				}
				.st1 {
					fill: none;
					stroke: var(--color-secondary);
					stroke-width: 19;
					stroke-linecap: round;
					stroke-miterlimit: 10;
					transform-origin: 50% 75%;
				}
			}
			.card {
				background-color: var(--color-primary);
				border-radius: 300px 300px var(--border-radius) var(--border-radius);
				text-align: center;
				border: none;
				.card-body {
					position: relative;
					padding: 1rem;
					h4 {
						padding: 0 0 1rem;
					}
					.event-name {
						position: relative;
						background-color: var(--color-main);
						color: var(--color-dark);
						border-radius: 300px;
						width: 100%;
						aspect-ratio: 5/6;
						display: flex;
						align-items: center;
						justify-content: center;
						flex-direction: column;
						margin-bottom: 5rem;
						&-body {
							position: relative;
							padding: 1rem;
						}
						.date {
							position: relative;
							width: 100%;
							padding: 1rem 0;
							h5 {
								font-family: $font-secondary;
								font-weight: 700;
								margin-bottom: 0;
								font-size: 32px;
								min-width: 30%;
							}
						}
						h4 {
							font-family: $font-secondary;
						}
						p {
							margin-bottom: 0;
							font-weight: 600;
						}
					}
					.event-description {
						position: relative;
						margin-bottom: 1rem;
					}
					.event-place {
						position: relative;
						background-color: var(--color-main);
						color: var(--color-dark);
						border-radius: var(--border-radius);
						padding: 1.5rem;
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		row-gap: 5rem;
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
