.thank-section {
	position: relative;
	padding: 5rem 0 10rem;
	text-align: center;
	@include max-md {
		padding: 3rem 0 8rem;
	}
	.orn-50 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 150%;
		transform: translate(-20%, 0);
		opacity: 0.15;
	}
	.orn-98 {
		position: relative;
		width: 50px;
		margin: 0 auto 1rem;
	}
	.orn-97 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		@include max-lg {
			bottom: 30%;
		}
	}
	.orn-96 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 100%;
		transform: translate(-50%, 0);
		@include max-lg {
			bottom: 3%;
			width: 150%;
		}
	}
	.orn-95 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		@include min-lg {
			transform: translate(10%, 0);
		}
		@include max-lg {
			right: -25%;
			width: 200%;
		}
	}
	.orn-94 {
		position: absolute;
		bottom: 8%;
		left: 50%;
		width: 240px;
		transform: translate(-50%, 0);
		@include max-md {
			bottom: 5%;
			width: 180px;
		}
	}
	.orn-1 {
		position: absolute;
		bottom: 20%;
		left: 0;
		width: 240px;
		transform: translate(-75%, 0);
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 20%;
		right: 0;
		width: 240px;
		transform: translate(75%, 0) rotate(-10deg);
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 50%;
		@include min-md {
			width: 45%;
		}
		@include min-lg {
			width: 30%;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 120px;
		transform: translate(-40%, 30%);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 50%;
		@include min-md {
			width: 45%;
		}
		@include min-lg {
			width: 30%;
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 120px;
		transform: translate(40%, 30%) rotate(-10deg);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-7 {
		position: absolute;
		top: 0;
		right: 0;
		width: 180px;
		transform: translate(20%, -80%);
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
	}
	.orn-8 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(0, -80%) rotate(10deg) scaleX(-1);
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
	}
	.image-wrapper {
		position: relative;
		width: 80%;
		height: auto;
		aspect-ratio: 3/4;
		margin: 0 auto;
		@include max-md {
			width: 100%;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 300px;
			}
		}
		.frame {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 120%;
			aspect-ratio: 3/4;
			transform: translate(-50%, -50%);
			.image-element {
				height: 100%;
				img {
					width: 100%;
					height: 100%;
				}
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			background-color: var(--color-primary);
			color: var(--color-light);
			border-radius: var(--border-radius);
			padding: 3rem 2rem;
			margin-bottom: 5rem;
			overflow: hidden;
			@include max-md {
				padding: 2rem 1rem;
			}
			p {
				margin-bottom: 0;
			}
			&-body {
				position: relative;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
