.couple-section {
	position: relative;
	background-color: var(--color-primary);
	color: var(--color-light);
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		padding: 1rem 0;
		z-index: 3;
		span {
			position: relative;
			font-size: 32px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
			.container-couple {
				padding: 0;
				.row {
					row-gap: 5rem;
				}
			}
		}
		.orn-99 {
			position: absolute;
			top: 0;
			right: 0;
			width: 70%;
			transform: translate(10%, -20%);
			opacity: 0.15;
			@include max-md {
				width: 100%;
			}
		}
		.orn-98 {
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 120%;
			transform: translate(-50%, 0);
			opacity: 0.15;
		}
		.orn-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 120px;
			transform: translate(-50%, 0);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		background-color: var(--color-main);
		color: var(--color-dark);
		border-radius: var(--border-radius);
		padding: 8rem 2rem 3rem;
		margin-bottom: 3rem;
		overflow: hidden;
		@include max-md {
			padding: 5rem 1rem 2rem;
		}
	}
	.couple {
		position: relative;
		text-align: center;
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 5/6;
			margin: 2rem auto 5rem;
			@include max-sm {
				width: 100%;
			}
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					width: 100%;
					border-radius: 300px;
				}
			}
			.frame {
				position: absolute;
				top: 50%;
				left: 50%;
				width: 118%;
				aspect-ratio: 3/4;
				transform: translate(-50%, -50%);
				.image-element {
					height: 100%;
					img {
						width: 100%;
						height: 100%;
					}
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem;
		}
		.couple-parent {
			position: relative;
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--text-button);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
