.story-section {
	position: relative;
	.story {
		position: relative;
		padding: 5rem 0 0;
		@include max-md {
			padding: 3rem 0 0;
		}
		.orn-99 {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			aspect-ratio: 16/9;
			@include max-md {
				aspect-ratio: 5/6;
			}
			.image-element {
				height: 100%;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					object-position: 50% 100%;
					opacity: 0.2;
				}
			}
		}
		.orn-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 120px;
			transform: translate(-50%, 0);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
		}
		.orn-2 {
			position: absolute;
			bottom: 5%;
			right: 0;
			width: 120px;
			transform: translate(50%, 0) scaleX(-1);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
		background-color: var(--color-primary);
		color: var(--color-light);
		border-radius: var(--border-radius);
		overflow: hidden;
		z-index: 3;
		&-body {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		margin-top: -3rem;
		@include max-md {
			aspect-ratio: 5/6;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
