.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.orn-99 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 120%;
		height: 100%;
		transform: translate(-50%, 0);
		.image-element {
			height: 100%;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: 50% 21%;
				@include max-md {
					object-fit: fill;
				}
			}
		}
		.orn-plus {
			position: absolute;
			top: 0;
			left: 50%;
			width: 125%;
			transform: translate(-50%, -30%);
			@include min-lg {
				display: none;
			}
		}
	}
	.orn-98 {
		position: relative;
		width: 50px;
		margin: 0 auto 1rem;
	}
	.orn-97 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		@include max-lg {
			bottom: 30%;
		}
	}
	.orn-96 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 100%;
		transform: translate(-50%, 0);
		@include max-lg {
			bottom: 3%;
			width: 150%;
		}
	}
	.orn-95 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		@include max-lg {
			right: -25%;
			width: 200%;
		}
	}
	.orn-94 {
		position: absolute;
		bottom: 8%;
		left: 50%;
		width: 240px;
		transform: translate(-50%, 0);
		@include max-md {
			bottom: 5%;
			width: 180px;
		}
	}
	.orn-1 {
		position: absolute;
		bottom: 10%;
		left: 0;
		width: 240px;
		transform: translate(-65%, 0);
		@include min-md {
			width: 280px;
			transform: translate(-45%, 0);
		}
		@include min-lg {
			width: 320px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 20%;
		right: 0;
		width: 240px;
		transform: translate(65%, 0) rotate(-10deg);
		@include min-md {
			top: 40%;
			width: 280px;
			transform: translate(45%, 0) rotate(-10deg);
		}
		@include min-lg {
			width: 320px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 50%;
		@include min-md {
			width: 45%;
		}
		@include min-lg {
			width: 35%;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 120px;
		transform: translate(-40%, 30%);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 50%;
		@include min-md {
			width: 45%;
		}
		@include min-lg {
			width: 35%;
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 120px;
		transform: translate(40%, 30%) rotate(-10deg);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.header-content {
		position: relative;
		min-height: 100vh;
		.header {
			position: relative;
			width: 35%;
			height: 100%;
			margin: 0 auto;
			padding: 5rem 0 0;
			@include max-xl {
				width: 40%;
			}
			@include max-lg {
				padding: 10rem 0 0;
				width: 70%;
			}
			@include max-md {
				padding: 8rem 0 0;
				width: 100%;
			}
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
					.title {
						line-height: 1;
					}
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
