.reminder-wrap {
	position: relative;
	display: block;
	padding: 5rem 0 0;
	@include max-md {
		padding: 3rem 0 0;
	}
	.orn-99 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 80%;
		.image-element {
			height: 100%;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: 50% 100%;
				opacity: 0.5;
			}
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 120px;
		transform: translate(-50%, 0);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 30%;
		right: 0;
		width: 120px;
		transform: translate(50%, 0) rotate(-10deg);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		transform: translate(-80%, 0);
		@include min-md {
			width: 220px;
			transform: translate(-70%, 0);
		}
		@include min-lg {
			width: 240px;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 180px;
		transform: translate(80%, 0) rotate(-10deg);
		@include min-md {
			width: 220px;
			transform: translate(70%, 0) rotate(-10deg);
		}
		@include min-lg {
			width: 240px;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		aspect-ratio: 3/4;
		margin: 2rem 0 5rem;
		.image-element {
			height: 100%;
			.reminder-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 300px;
			}
		}
		.frame {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 120%;
			aspect-ratio: 3/4;
			transform: translate(-50%, -50%);
			.image-element {
				height: 100%;
				img {
					width: 100%;
					height: 100%;
				}
			}
		}
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		max-width: 100%;
		margin: 0 auto;
		z-index: 2;
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
