.wishes-section {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0 0;
	}
	.orn-98 {
		position: absolute;
		top: 0;
		left: 50%;
		width: 50%;
		transform: translate(-40%, -20%);
		opacity: 0.15;
		@include max-lg {
			width: 80%;
		}
		@include max-md {
			width: 100%;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-80%, -20%);
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 120px;
		transform: translate(50%, -20%) scaleX(-1);
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 120px;
		transform: translate(55%, 10%);
	}
	.wishes-form-wrapper {
		position: relative;
		--color-button-trans: #1e1f1e;
	}
	.card-form {
		background-color: transparent;
		&.card {
			border: none;
		}
		.card-body {
			padding: 3rem 3rem 3rem 5rem;
			@include max-lg {
				padding: 2rem 1.8rem;
			}
			textarea.form-control {
				border: 1px solid var(--color-button-trans);
			}
		}
	}

	.wishes-preview {
		position: relative;
		background-color: var(--color-primary);
		color: var(--color-light);
		overflow: hidden;
		@include max-md {
			padding: 0 0 3rem;
		}
		.orn-99 {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 70%;
			.image-element {
				height: 100%;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					object-position: 50% 100%;
					opacity: 0.5;
				}
			}
		}
		.card {
			position: relative;
			border: none;
			background-color: transparent;
			.card-body {
				overflow: hidden;
				padding: 3rem 5rem 3rem 3rem;
				@include max-lg {
					padding: 2rem 1.8rem;
				}
				.wishes {
					text-align: left;
					max-height: 35rem;
					overflow: auto;
					padding-right: 1rem;
					color: var(--color-dark);
					&::-webkit-scrollbar {
						width: 5px;
					}
					&::-webkit-scrollbar-track {
						background-color: #eee;
						border-radius: 20px;
						opacity: 0.15;
					}
					&::-webkit-scrollbar-thumb {
						background-color: var(--color-secondary);
						border-radius: 20px;
					}
					// .text-muted {
					// 	color: rgba($color: #fff, $alpha: 0.8) !important;
					// }
					.wish {
						margin-bottom: 1.5rem;
						display: grid;
						grid-template-columns: auto 1fr;
						gap: 1rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
						.wish-badge {
							background-color: var(--color-light);
							width: 35px;
							aspect-ratio: 1;
							display: flex;
							justify-content: center;
							align-items: center;
							border-radius: 50px;
						}
						.wish-description {
							padding: 1rem;
							background-color: rgba($color: #fff, $alpha: 0.8);
							border-radius: 5px;
							position: relative;
							&::before {
								position: absolute;
								top: 15px;
								left: 0;
								width: 15px;
								height: 15px;
								transform: translateX(-100%);
								clip-path: url(#wishes-polygon);
								content: "";
								background-color: rgba($color: #fff, $alpha: 0.8);
							}
						}
					}
				}
			}
		}
	}
}
